<template>
	<div class="emb-register-wrap section-gap">
		<div class="container py-0">
			<v-layout row wrap align-center justify-center>
				<v-flex sm12 md12 lg8 xl7>
					<v-layout row mx-sm-0 mx-3  wrap align-center justify-center>
						
						<v-flex sm10 md5 lg6 xl6>
							<div class="emb-card sign-in-form form-margin d-block white pa-6">
								<h4>Enter Your Departments details</h4>
								<v-form ref="form" >
                                <v-text-field prepend-icon="edit" label="English Name* " v-model="Departments.en" required></v-text-field>
						       <v-text-field prepend-icon="edit" label="kinyarwanda Name*" v-model="Departments.kiny" required></v-text-field>
						       <v-text-field prepend-icon="edit" label="french Name*" v-model="Departments.fr" required></v-text-field>
                                <v-text-field prepend-icon="edit" label="swahili Name*" v-model="Departments.sw" required></v-text-field>
								<v-btn class="accent mx-0 mb-4 mt-4" large  @click.stop.prevent="editdep">
										Edit Departments
									</v-btn>
								</v-form>
							</div>
						</v-flex>
					</v-layout>
				</v-flex>
			</v-layout>
		</div>
   </div>
</template>

<script>
import Departments from "Api/department";

export default {
  data() {
    return {
        Departments: {
                   en: '',
                   fr: '',
                   kiny: '',
                   sw: '',
                   },
      checkbox: false,
      products: null,
      selectedProduct: null,
      selectedImage: null
    };
  },
  async mounted() {
	// this.getProductsData();
	try {
	this.id = this.$route.params.id;
    const res = await Departments.getDepartment()
    console.log('hhh')
	res.data.data.forEach(el => {
		if (el._id === this.id) {
            this.Departments.en = el.name.en,
            this.Departments.fr = el.name.fr,
            this.Departments.kiny = el.name.kiny
            this.Departments.sw = el.name.sw
            console.log(this.Departments)
		}
    });
	} catch (err) {
		console.log(err.message)
	}
  },
  methods: {
      async editdep() {
          try {
              const resUpdate = await Departments
              .updateDepartment({
                 name: this.Departments 
            }, this.id)
            console.log(resUpdate)
            this.$router.push('/admin-panel/Departments')
          } catch (err) {
              console.log(err.message)
          }
      },
    /* for toggling image **/
    doHover(image) {
      this.selectedImage = image;
    }
  }
};
</script>
